/* Home Header */

.home_header {
  background: url('../../assets/images/landing.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  text-align: center;
  padding: 15px;
  margin: 0;
}

.home_header h1 {
  width: 60%;
  min-width: 175px;
  margin: 0 auto;
  background-color: rgba(0, 0, 0, 0.5);
  text-transform: uppercase;
  border: 1px solid white;
  font-family: Dosis, sans-serif;
  font-size: 2rem;
  text-align: center;
  padding: 10px;
  color: white;
  clear: both;
}

.home_header span {
  color: #f88f3d;
}

.home_header button,
.home_header button:focus {
  background-color: white;
  color: #f88f3d;
  border: 2px solid #f88f3d;
  font-size: 20px;
  font-weight: bold;
  padding: 5px 10px;
  line-height: 10px;
  height: 40px;
  margin-top: 15px;
  min-width: 175px;
  z-index: 0;
  transition: 0.5s ease;
}

.home_header button:hover {
  background-color: #f88f3d;
  color: #FFF;
  transition: 0.5s ease;
}

/* Page Content */

.home_content {
  padding-top: 20px;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 0;
}

.home_content .medals {
  margin: 0 auto;
  text-align: center;
  min-width: 260px;
}

.home_content .medals .medal {
  float: left;
  margin: 0;
  border-radius: 10px;
  padding: 10px;
  width: 350px;
  height: 330px;
}

@media screen and ( max-width: 529px ) {
  .home_content .medals .medal {
    margin-bottom: 40px;
  }
}

@media screen and ( min-width: 530px) and (max-width: 880px) {
  .home_content .medals .medal {
    max-width: 300px;
    margin-bottom: 40px;
  }
}

@media screen and ( min-width: 881px) {
  .home_content .medals .medal {
    max-width: 400px;
  }
}

.home_content .medal h1 {
  color: #9e9e9e;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: bold;
  margin: 10px auto;
  text-align: center;
}

.home_content .medal .number {
  color: #72C168;
  font-size: 30px;
  font-weight: bold;
  margin: 5px;
}

.home_content .medal img {
  height: 250px;
}

.home_kudos {
  padding: 0;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.home_kudos .badges {
  margin: 0 auto;
  text-align: center;
  min-width: 260px;
  background: #F4F6F9;
  border-radius: 10px;
  padding: 5px 15px;
}

.home_kudos .badges h3 {
  color: #9e9e9e;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: bold;
  margin: 10px auto;
}

.home_kudos .badges img{
  width: 100px;
  padding: 10px;
  box-shadow: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
}

/* Toasts */

.toast {
  top: 0;
}

.msgToast {
  background-color: #333333;
  border: 2px solid #9c9c9c;
  border-radius: 20px;
  color: #FFF;
  font-size: 16px;
  text-transform: uppercase;
  padding: 15px;
}

.msgToast span {
  color: #72C168;
  padding-left: 5px;
  font-size: 18px;
  font-weight: bold; 
}

.nominationsToast {
  background-color: #333333;
  border: 2px solid #9c9c9c;
  border-radius: 20px;
  color: #FFF;
  font-size: 16px;
  text-transform: uppercase;
  padding: 15px;
}

.nominationsToast span {
  color: #08C1AF;
  padding: 0 10px;
  font-weight: bold;
  font-size: 18px;
}

.badge-disabled {
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
  opacity: 0.5;
}

/* TOOLTIPS */

.wrapper {
  float: left;
  -webkit-transform: translateZ(0); /* webkit flicker fix */
  -webkit-font-smoothing: antialiased; /* webkit text rendering fix */
  z-index: 10;
}

.wrapper .tooltip {
  background: #585a61;
  border-radius: 10px;
  bottom: 100%;
  color: #F4F6F9;
  display: block;
  left: -49px;
  margin-bottom: 15px;
  opacity: 0;
  padding: 20px;
  pointer-events: none;
  position: absolute;
  width: 200px;
  -webkit-transform: translateY(10px);
     -moz-transform: translateY(10px);
      -ms-transform: translateY(10px);
       -o-transform: translateY(10px);
          transform: translateY(10px);
  -webkit-transition: all .25s ease-out;
     -moz-transition: all .25s ease-out;
      -ms-transition: all .25s ease-out;
       -o-transition: all .25s ease-out;
          transition: all .25s ease-out;
  -webkit-box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.28);
     -moz-box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.28);
      -ms-box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.28);
       -o-box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.28);
          box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.28);
  z-index: 11;
}

.wrapper .tooltip span {
  color: #72C168;
  font-weight: bold;
  text-transform: uppercase;
  margin-right: 5px;
}

/* This bridges the gap so you can mouse into the tooltip without it disappearing */
.wrapper .tooltip:before {
  bottom: -20px;
  content: " ";
  display: block;
  height: 20px;
  left: 0;
  position: absolute;
  width: 100%;
}  

/* CSS Triangles - see Trevor's post */
.wrapper .tooltip:after {
  border-left: solid transparent 10px;
  border-right: solid transparent 10px;
  border-top: solid #585a61 10px;
  bottom: -10px;
  content: " ";
  height: 0;
  left: 50%;
  margin-left: -10px;
  position: absolute;
  width: 0;
}
  
.wrapper:hover .tooltip {
  opacity: 1;
  pointer-events: auto;
  -webkit-transform: translateY(0px);
     -moz-transform: translateY(0px);
      -ms-transform: translateY(0px);
       -o-transform: translateY(0px);
          transform: translateY(0px);
  z-index: 12;
}

/* IE can just show/hide with no transition */
.lte8 .wrapper .tooltip {
  display: none;
}

.lte8 .wrapper:hover .tooltip {
  display: block;
}