nav.navbar-intersys {
  background-color: #ffffff;
  border-bottom: 1px solid #f1f3f5;
  box-shadow: none;
  font-family: Dosis, sans-serif;
  height: 65px; }
  nav.navbar-intersys .logo {
    height: 25px;
    margin-left: 15px;
    margin-top: -5px; }
  nav.navbar-intersys ul div {
    display: inline !important; }
  nav.navbar-intersys li {
    height: 65px; }
    nav.navbar-intersys li a {
      color: #4E5056;
      font-size: 16px;
      height: 65px;
      text-transform: uppercase; }
      nav.navbar-intersys li a i {
        font-size: 22px; }
      nav.navbar-intersys li a:hover {
        background-color: #f6f7f8;
        border-bottom: 2px solid #72c168; }
    nav.navbar-intersys li p {
      color: #4E5056;
      font-size: 16px;
      height: 65px;
      text-transform: uppercase; }
      nav.navbar-intersys li p i {
        font-size: 22px; }
    nav.navbar-intersys li.sign-out p {
      color: #f88f3d; }
      nav.navbar-intersys li.sign-out p:hover {
        background-color: #f88f3d;
        color: #ffffff; }
  nav.navbar-intersys a.button-collapse i {
    color: #4E5056;
    font-size: 30px; }
  nav.navbar-intersys .dropdown-content {
    min-width: 180px;
    text-align: center;
    left: initial;
    right: 0;
    overflow: hidden; }
    nav.navbar-intersys .dropdown-content li a {
      line-height: 37px; }
    nav.navbar-intersys .dropdown-content li p {
      line-height: 37px;
      margin: 0;
      padding: 14px 16px; }
      nav.navbar-intersys .dropdown-content li p i {
        line-height: 37px; }
  nav.navbar-intersys .side-nav li a,
  nav.navbar-intersys .side-nav li a i {
    color: #4E5056;
    line-height: 65px; }
  nav.navbar-intersys .side-nav .dropdown-content li a,
  nav.navbar-intersys .side-nav .dropdown-content li a i {
    line-height: 37px;
    margin: 0; }
