/* Tooltips */

.medal_tooltip {
  font-size: 16px;
}

.medal_tooltip .number {
  color: #f88f3d;
  font-size: 30px;
  font-weight: bold;
  margin: 0;
}

.medal_tooltip .text {
  color: #FFF;
  margin: 0;
  font-size: 16px;
  text-transform: initial;
}

.medal_tooltip .level {
  margin: 0;
  color: #72C168;
  font-weight: bold;
  font-size: 18px;
  text-transform: uppercase;
}

.medal_tooltip .levelNext {
  margin: 0;
  color: #f88f3d;
  font-weight: bold;
  font-size: 18px;
  text-transform: uppercase;
}


.medal-wrapper .tooltip {
  background-color: rgba(88, 90, 97,0.9)!important;
  border: 1px solid #f88f3d;
  left: 60px !important;
  width: 230px !important;
}
.medal-wrapper .tooltip::after {
  border-top: solid rgba(88, 90, 97,0.9) 10px !important;
}