.nominationHeader {
  background: url('../../assets/images/nomination.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 35%;
  width: 100%;
  text-align: center;
  padding: 20px;
}

.nominationHeader h1 {
  width: 60%;
  min-width: 180px;
  margin: 0 auto;
  background-color: rgba(0, 0, 0, 0.5);
  text-transform: uppercase;
  border: 1px solid white;
  font-family: Dosis, sans-serif;
  font-size: 2rem;
  text-align: center;
  padding: 10px;
  color: white;
  clear: both;
}

.nomination {
  padding: 1em;
  max-width: 1200px !important;
  text-align: center;
}

.nominationForm .active {
  color: #146CB5 !important;
}

.nominationForm input {
  color: black;
}

.nominationForm input:focus, .nominationForm textarea:focus {
  border-bottom: 1px solid #146CB5 !important;
  box-shadow: 0 1px 0 0 #146CB5 !important;
}

.input-field .prefix~input, .input-field .prefix~textarea, .input-field .prefix~label {
  margin: 0 !important;
}

.input-field .prefix~input {
  width: 100% !important;
}

.nominationForm .input-field {
  margin-top: 35px;
}

.nominationForm .input-field .prefix {
  background-color: white;
  font-size: 16px;
  margin-right: 10px !important;
  margin-top: 12px !important;
  padding: 0;
  padding-right: 0.75rem;
  right: 0;
  width: auto;
  z-index: 2;
}

.nominationForm .valid {
  border-bottom: 1px solid #72C168;
}

.nominationForm label {
  text-align: left;
}

.nominationForm label::after {
  padding-top: 5px;
}

.nominationForm i {
  color: #9e9e9e
}

.nominationForm .emailComplete {
  margin-top: 1.8rem;
  padding: 0;
  position: relative;
  text-align: left;
}

.nominationFormButtons {
  margin-top: 40px;
}

.nominationFormButtons button {
  background-color: #08C1AF;
  color: #FFF;
  border: 2px solid transparent;
  font-size: 20px;
  font-weight: bold;
  padding: 10px;
  height: 60px;
  margin: 10px;
}

.nominationFormButtons button.sendButton {
  background-color: #72C168;
  color: #FFF;
}

.nominationFormButtons button.sendClearButton {
  border: 2px solid #146CB5;
  background-color: #FFF;
  color: #146CB5;
}

.nominationFormButtons button.sendButton:hover {
  background-color: #57934f;
}

.nominationFormButtons button.sendClearButton:hover {
  background-color: #146CB5;
  color: #FFF;
}

.nomination .valueTitle, .nominationForm h1 {
  color: #9e9e9e;
  font-size: 1.2rem;
  text-align: center;
}

.intersysValue,
.intersysValue:focus {
  width: 180px;
  height: 180px;
  padding: 20px;
  border-radius: 100px;
  cursor: pointer;
  color: #FFF;
  font-weight: bold;
  font-size: 1.3rem;
  margin: 10px;
  /*opacity: 0.5;*/
  box-shadow: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  -webkit-transition: 0.5s;
  /* Safari */
  transition: 0.5s;
}

.intersysValue:hover {
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
  height: 190px;
  width: 190px;
  padding: 25px;
  margin-bottom: 0;
}

.beacountable, .beacountable:focus, .beacountable:hover {
  background-color: #72C168;
}

.beauthentic, .beauthentic:focus, .beauthentic:hover {
  background-color: #f88f3d;
}

.beinservice, .beinservice:focus, .beinservice:hover {
  background-color: #7962c6;
}

.bringexcellence, .bringexcellence:focus, .bringexcellence:hover {
  background-color: #146CB5;
}

.nonSelected {
  background-color: #dadada;
}

.valueSelected, .valueSelected:hover {
  -webkit-box-shadow: 0px 0px 20px 1px rgba(20, 108, 181, 0.5);
  -moz-box-shadow: 0px 0px 20px 1px rgba(20, 108, 181, 0.5);
  box-shadow: 0px 0px 20px 1px rgba(20, 108, 181, 0.5);
  /*opacity: 1;*/
  height: 180px;
  width: 180px;
  padding: 20px;
  margin: 10px;
}

.msgToast {
  background-color: #333333;
  border: 2px solid #9c9c9c;
  border-radius: 20px;
  color: #FFF;
  font-size: 16px;
  text-transform: uppercase;
  padding: 15px;
}

.msgToast span {
  color: #72C168;
  padding-left: 5px;
  font-size: 18px;
  font-weight: bold; 
}

.errorToast {
  background-color: #333333;
  border: 2px solid #f88f3d;
  border-radius: 20px;
  color: #FFF;
  font-size: 16px;
  text-transform: uppercase;
  padding: 15px;
}

.errorToast span {
  color: #f88f3d;
  padding-left: 5px;
  font-size: 18px;
  font-weight: bold; 
}

.select-dropdown img {
  border-radius: 50%;
}

.input:not([type])+label:after, input[type=text]+label:after, input[type=password]+label:after, input[type=email]+label:after, input[type=url]+label:after, input[type=time]+label:after, input[type=date]+label:after, input[type=datetime]+label:after, input[type=datetime-local]+label:after, input[type=tel]+label:after, input[type=number]+label:after, input[type=search]+label:after, textarea.materialize-textarea+label:after {
  top: 40px;
  font-size: 12px;
  min-width: 150px;
}

.btn.disabled {
  border: 2px solid transparent !important;
}